<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区 -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="10">
          <!-- 搜索输入框 -->
          <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
            <!-- 搜索按钮 -->
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <!-- 添加按钮 -->
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 用户列表区域 -->
      <el-table :data="userlist" stripe border>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column label="电话" prop="mobile"></el-table-column>
        <el-table-column label="邮箱" prop="email"></el-table-column>
        <el-table-column label="角色" prop="role_name"></el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180">
          <template slot-scope="scope">
            <!-- 修改按钮 -->
            <el-button @click="showEditDialog(scope.row.id)" type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!-- 删除按钮 -->
            <el-button @click="removeUserById(scope.row.id)" type="danger" icon="el-icon-delete" size="mini"></el-button>
            <!-- 分配角色按钮 -->
            <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 7, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!-- 添加用户对话框 -->
    <el-dialog
      @close="addDialogClosed"
      title="添加用户"
      :visible.sync="addDialogVisible"
      width="50%">
      <!-- 对话框主体 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <!-- 按钮 -->
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改用户对话框 -->
    <el-dialog
      @close="editDialogClosed"
      title="编辑用户"
      :visible.sync="editDialogVisible"
      width="50%">
      <!-- 对话框主体 -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <!-- 按钮 -->
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 分配角色的对话框 -->
    <el-dialog
      @close="setRoleDialogClosed"
      title="分配角色"
      :visible.sync="setRoleDialogVisible"
      width="50%">
      <!-- 对话框主体 -->
      <el-form ref="editFormRef" label-width="70px">
        <el-form-item label="用户名">
          <el-input v-model="userinfo.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="角色名">
          <el-select v-model="selectedRoleId" :placeholder="userinfo.role_name">
            <el-option
              v-for="item in roleslist"
              :key="item.id"
              :label="item.roleName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'users',
  data() {
    // 验证邮箱合法性
    const checkEmail = (rule, value, callback) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        // 合法邮箱
        return callback()
      }
      // 邮箱不合法
      return callback(new Error('请输入合法邮箱'))
    }
    // 验证手机号合法性
    const checkMobile = (rule, value, callback) => {
      const regEmail = /^(0|86|17951)?(13[0-9]|15[012356789]|[17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regEmail.test(value)) {
        // 合法手机号
        return callback()
      }
      // 手机号不合法
      return callback(new Error('请输入合法手机号'))
    }
    return {
      // 查询的信息参数
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 用户列表
      userlist: [],
      // 用户列表总数
      total: 0,
      // 控制添加用户对话框的显示
      addDialogVisible: false,
      // 控制编辑用户对话框的显示
      editDialogVisible: false,
      // 添加用户的表格绑定的数据
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 编辑用户的表格绑定的数据
      editForm: {},
      // 添加用户的表格的验证规则
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 编辑用户的表格的验证规则
      editFormRules: {
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 控制分配角色对话框的显示
      setRoleDialogVisible: false,
      // 当前正在分配角色的用户的信息
      userinfo: {},
      // 角色列表
      roleslist: [],
      // 为用户分配角色时选中的角色id
      selectedRoleId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    // 获取用户列表
    async getUserList() {
      const { data: res } = await this.$http.get('/users', { params: this.queryInfo })
      // 获取用户列表失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 获取用户列表成功
      this.userlist = res.data.users
      this.total = res.data.total
    },
    // 监听pages变化
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 监听页码变化
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 监听用户状态改变
    async userStateChanged(userinfo) {
      const { data: res } = await this.$http.put(`/users/${userinfo.id}/state/${userinfo.mg_state}`)
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error(res.meta.msg)
      }
      this.$message.success(res.meta.msg)
    },
    // 监听添加用户对话框关闭
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 添加用户
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        // 表单验证失败
        if (!valid) return
        // 表单验证成功，发送请求
        const { data: res } = await this.$http.post('/users', this.addForm)
        // 添加失败
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        // 添加成功
        this.$message.success(res.meta.msg)
        // 关闭对话框
        this.addDialogVisible = false
        // 刷新列表
        this.getUserList()
      })
    },
    // 展示编辑用户对话框
    async showEditDialog(id) {
      // 查询用户数据
      const { data: res } = await this.$http.get('/users/' + id)
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      // console.log(res.data)
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 监听编辑用户对话框关闭
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 编辑用户
    editUserInfo() {
      // 表单预验证
      this.$refs.editFormRef.validate(async valid => {
        // 验证失败
        if (!valid) return
        // 验证成功，发起修改请求
        const { data: res } = await this.$http.put('/users/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        })
        // 修改失败
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        // 修改成功
        this.$message.success(res.meta.msg)
        // 关闭对话框
        this.editDialogVisible = false
        // 刷新列表
        this.getUserList()
      })
    },
    // 通过id删除用户
    async removeUserById(id) {
      // 弹出提示框
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 取消删除操作
      if (confirmResult !== 'confirm') return this.$message.info('取消了删除操作')
      // 确认删除，发起删除请求
      const { data: res } = await this.$http.delete('/users/' + id)
      // 删除失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 删除成功
      this.$message.success(res.meta.msg)
      // 刷新列表
      this.getUserList()
    },
    // 为用户分配角色
    async setRole(userinfo) {
      this.userinfo = userinfo
      // 获取角色列表
      const { data: res } = await this.$http.get('/roles')
      // 获取失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 获取成功
      this.$message.success(res.meta.msg)
      // 绑定数据
      this.roleslist = res.data
      // console.log(res.data)
      // 展示对话框
      this.setRoleDialogVisible = true
    },
    // 保存所选角色
    async saveRoleInfo() {
      if (!this.selectedRoleId) return this.$message.error('请选择新的角色')
      // 发送保存角色的请求
      const { data: res } = await this.$http.put(`/users/${this.userinfo.id}/role`, { rid: this.selectedRoleId })
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 隐藏分配角色对话框
      this.setRoleDialogVisible = false
    },
    // 监听分配角色对话框关闭
    setRoleDialogClosed() {
      this.selectedRoleId = ''
      this.userinfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
.el-select {
  width: 100%;
}
</style>
